<template>
    <CCard>
        <v-progress-linear
            :active="fetchLoading"
            indeterminate
            color="cyan"
        ></v-progress-linear>
        <CCardBody>
            <CCard class="p-4 border-0">
                <CCardBody>
                    <form v-on:submit.prevent>
                        <h4>Expenses Details</h4>
                        <hr />

                        <v-text-field
                            v-model="formData.cheque_issuer"
                            label="Cheque Issuer"
                            :error="errors.cheque_issuer ? true : false"
                            :error-messages="errors.cheque_issuer"
                            required
                            outlined
                            dense
                            clearable
                        ></v-text-field>
                        <v-text-field
                            v-model="formData.signer"
                            label="Signer"
                            :error="errors.signer ? true : false"
                            :error-messages="errors.signer"
                            required
                            outlined
                            dense
                            clearable
                        ></v-text-field>
                        <v-text-field
                            v-model="formData.issued_company"
                            label="Issued Company"
                            :error="errors.issued_company ? true : false"
                            :error-messages="errors.issued_company"
                            required
                            outlined
                            dense
                            clearable
                        ></v-text-field>
                        <v-text-field
                            v-model="formData.pay_to"
                            label="Pay to"
                            :error="errors.pay_to ? true : false"
                            :error-messages="errors.pay_to"
                            required
                            outlined
                            dense
                            clearable
                        ></v-text-field>
                        <v-text-field
                            v-model="formData.amount"
                            label="Amount"
                            type="number"
                            :error="errors.amount ? true : false"
                            :error-messages="errors.amount"
                            required
                            outlined
                            dense
                            clearable
                        ></v-text-field>
                        <v-menu
                            v-model="chequeIssuedDateMenu"
                            :close-on-content-click="false"
                            :nudge-right="40"
                            transition="scale-transition"
                            offset-y
                            min-width="auto"
                        >
                            <template v-slot:activator="{ on, attrs }">
                                <v-text-field
                                    v-model="formData.cheque_issued_date"
                                    label="Cheque Issued Date"
                                    outlined
                                    dense
                                    clearable
                                    readonly
                                    :error="
                                        errors.cheque_issued_date ? true : false
                                    "
                                    :error-messages="errors.cheque_issued_date"
                                    v-bind="attrs"
                                    v-on="on"
                                ></v-text-field>
                            </template>
                            <v-date-picker
                                v-model="formData.cheque_issued_date"
                                @input="chequeIssuedDateMenu = false"
                            ></v-date-picker>
                        </v-menu>
                        <v-text-field
                            v-model="formData.item"
                            label="Item"
                            :error="errors.item ? true : false"
                            :error-messages="errors.item"
                            required
                            outlined
                            dense
                            clearable
                        ></v-text-field>
                        <hr />
                        <CButton @click="update" color="primary" class="px-4">
                            Update
                            <v-progress-circular
                                v-if="updateLoading"
                                indeterminate
                                color="primary"
                                :size="15"
                            ></v-progress-circular>
                        </CButton>
                    </form>
                </CCardBody>
            </CCard>
        </CCardBody>
    </CCard>
</template>

<script>
export default {
    name: "ExpensesDetails",
    components: {},
    data() {
        return {
            formData: {},
            chequeIssuedDateMenu: false,
            fetchLoading: false,
            updateLoading: false,
            errors: {}
        };
    },
    mounted() {
        this.fetch();
    },
    methods: {
        fetch() {
            let self = this;
            self.fetchLoading = true;
            let data = {
                id: self.$route.params.id
            };
            this.$store
                .dispatch("expenses/details", data)
                .then(response => {
                    self.formData = response.data.data;
                    self.fetchLoading = false;
                })
                .catch(error => {
                    self.fetchLoading = false;
                });
        },
        update() {
            let self = this;
            if (self.updateLoading) {
                return;
            }
            self.updateLoading = true;
            this.$store
                .dispatch("expenses/update", self.formData)
                .then(() => {
                    self.updateLoading = false;
                    self.errors = {};
                })
                .catch(error => {
                    self.updateLoading = false;
                    self.errors = error.response.data?.data;
                });
        }
    }
};
</script>

<style scoped>
.v-text-field >>> input {
    font-size: 0.8em;
    font-weight: 100;
}
.v-text-field >>> label {
    font-size: 0.8em;
}
.v-text-field >>> button {
    font-size: 0.8em;
}
</style>
